<style lang="scss">
    html {
        box-sizing: border-box;
        scroll-behavior: smooth;
        font-family: "Roboto", sans-serif;
    }

    *,
    *::before,
    *::after {
        box-sizing: inherit;
    }

    a {
        text-decoration: none;
        color: inherit;
        display: inline-block;
        width: max-content;
    }

    ul,
    li {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: 0;
        font-size: inherit;
    }

    p {
        margin: 0;
    }

    body {
        background-color: #ffffff;
        color: #2f3035;
        font-size: 18px;
        line-height: 24px;
        overflow-x: hidden;
        min-width: 320px;
        margin: 0;
    }

    .container {
        max-width: 1200px;
        padding: 0 30px;
        margin: 0 auto;
    }

    .page-section {
        margin-bottom: 80px;
        position: relative;
    }

    .line::before {
        content: "";
        position: absolute;
        width: 250px;
        height: 2px;
        background-color: red;
        left: -300px;
        top: 10px;
    }

    .title {
        font-size: 25px;
        padding-left: 80px;
        margin-bottom: 40px;
    }

    .descr {
        display: inline-block;
        margin-bottom: 40px;
    }

    .main {
        background-color: black;
        margin-bottom: 80px;
    }

    .splide .splide--loop .splide--ltr .splide--draggable .is-active {
      margin-left: 85px;
    }

    .splide__slide {
        filter: opacity(0.2);
    }

    .splide__slide + .is-active {
        filter: opacity(1);
    }

    .splide__pagination__page.is-active {
        background: red;
    }

    .splide__pagination {
        bottom: -1.5em;
    }

    .splide__arrow--prev {
        left: -2.6em;
    }

    .splide__arrow--next {
        right: -2.6em;
    }

    .divider:after,
    .handle {
        cursor: col-resize;
    }

    .img-box {
        position: relative;

        &__btn {
            color: #ffffff;
            position: absolute;
            width: 15px;
            height: 15px;
            background-color: red;
            top: 0;
            left: 0;
            cursor: pointer;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
</style>
